}

.pageLinks {
    height: 25rem;
}

.socials {
    height: 0;
    margin-bottom: 1rem;
}

.hidden {
    display: none;
}
.logo {
    height: 100%;
    display: flex;
    justify-content: center;
}
body {
    color: white;
}

h2 {
    font-size: 50px;
    margin: 20px 0 30px 0;
}

img {
    display: flex;
    align-content: center;
    width: 250px;
}

p {
    font-size: 20px;
}

.aboutMeContent {
    padding-top: 50px;
    width: 60%;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}

@media only screen and (max-width: 1800px) {
    .content {
        height: 70%;
    }
}

@media only screen and (max-width: 1070px) {
    .content {
        height: 80%;
    }
}

@media only screen and (max-width: 500px) {
    .content {
        height: 90%;
    }
}
.contain {
    position: relative;
}

.myWorkRow {
    width: 100%;
    margin: 0;
    padding: 0;
}

.overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 0;
    z-index: 1;
    color: white;
    transition: 0.3s ease-in-out;
    overflow: hidden;

    

    display: grid;
    grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
    grid-template-rows: min-content 1fr min-content;
}

.title {
    margin-top: 10px;
}

.description {
    margin-top: 20px;
}

.contain:hover img {
    transition: 0.3s ease-in-out;
    opacity: 0.2;
}

.contain:hover .overlay {
    transition: 0.5s ease-in-out;
    opacity: 1;
    cursor: pointer;
}

.mywork {
    width: 100%;
    overflow: hidden;
    transition: 0.5s ease-in-out;
    opacity: 1;
}

.experify {
    position: relative;
    
    justify-content: center;
    align-items: center;

    margin: 50px auto;
}

.experify img {
    width: 20%;

    opacity: 1;
}

.experify:hover img {
    transition: 0.3s ease-in-out;
    opacity: 0.2;
}

.experify:hover .experifyOverlay {
    transition: 0.5s ease-in-out;
    opacity: 1;
    cursor: pointer;
}

.experifyOverlay {
    position: absolute;
    height: 100%;
    width: 60%;
    opacity: 0;
    z-index: 1;
    color: white;
    transition: 0.3s ease-in-out;
    overflow: hidden;

    display: grid;
    grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
    grid-template-rows: min-content 1fr min-content;
}

.experifyLink:link {
    color: white;
    text-decoration: none;
}

.experifyLink:visited {
    color: white;
    text-decoration: none;
}

@media only screen and (max-width: 990px) {
    .mywork {
        margin-bottom: 20px;
    }

    .experify img {
        width: 50%;
    }

    .experifyOverlay {
        width: 90%;
    }
}
.contain {
    justify-content: center;
    align-items: center;

    height: 100%;
}

.contactMe {
    font-size: 16.5px;
}

.contact-links {
    margin: 10px;
    justify-content: center;
}

.formInput {
    background-color: #0A0A0A;
    color: white;
    border: none;
}

.formInput:focus {
    background-color: #0A0A0A;
    color: white;
}

.form-control.is-valid, .was-validated .form-control:valid {
    background-image: none;
}

.errorText {
    margin-top: 10px;
}

.submit {
    color: white;
}

.formInputRequired::after {
    content: ' *';
    color: #dc3545;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0 20px #0A0A0A inset;
}
